
import Vue from 'vue'
import layer from '@/static/js/layer/layer' //
import '@/static/js/layer/need/layer.css' //
let notice={
        msg: function(text){
            //
            layer.open({
                content: text
                ,skin: 'msg'
                ,styles: 'bottom:0;'
                ,time: 2 //2
              });
        },
        alert:function(text,btn,yes){
            //ios
            layer.open({
                content: text
                ,skin:'lzAlert'
                ,btn: btn || '',
                yes: function (index) {
                    layer.close(index);
                    if(typeof yes == 'function')yes(index);
                },
              });
        },
        footer:function(text,type){
            //
            layer.open({ 
                content: text
                ,skin: 'footer',
                shadeClose: type ? false : true
              });
        },
        loading:function(text){
            //
            var loading=layer.open({
                type: 2
                ,content: text
                ,shadeClose:false
              });
            return loading
        },
        custom:function(html,className,callback){
            // 
            layer.open({
                type: 1,
                content: html,
                className: className || '',
                shadeClose: true,
                end: function () {
                    callback()
                }
            })
        },
        /**
         * 
         * text-
         * btns-
         * yes-
         * no-
         */
        inquiry:function(text,btns,yes,no){
            var inquiry=layer.open({
                content: text
                ,btn: btns
                ,skin:"inquiry"
                ,yes: function(index){
                    layer.close(inquiry);
                    if(yes)yes(index);
                },
                no:function(){
                    if(no){
                        no();
                    }
                    
                }
              });
        },
        close(index){
            layer.close(index)
        },
        closeAll(){
            layer.closeAll()
        }   
    }


export default notice