import http from './http'
const browser = {
	versions: function () {
		var u = navigator.userAgent;
		return {
			trident: u.indexOf('Trident') > -1, //IE
			presto: u.indexOf('Presto') > -1, //opera
			webKit: u.indexOf('AppleWebKit') > -1, //
			gecko: u.indexOf('Firefox') > -1, //Gecko
			mobile: !!u.match(/AppleWebKit.*Mobile.*/), //
			ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
			android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //android
			iPhone: u.indexOf('iPhone') > -1, //iPhone
			iPad: u.indexOf('iPad') > -1, //iPad
			webApp: u.indexOf('Safari') > -1, //Safari
			user: u,
		};
	}()
}

// 
var tools = {
	android: browser.versions.android,//
	trident: browser.versions.trident, //IE
	presto: browser.versions.presto, //opera
	webKit: browser.versions.webKit, //
	gecko: browser.versions.gecko, //Gecko
	mobile: browser.versions.mobile, //
	ios: browser.versions.ios, //ios
	iPhone: browser.versions.iPhone, //iPhone
	iPad: browser.versions.iPad, //iPad
	webApp: browser.versions.webApp, //Safari
	user: browser.versions.user, //Safari
	//
	isWeiXin: function () {
		var ua = window.navigator.userAgent.toLowerCase();
		if (ua.match(/MicroMessenger/i) == 'micromessenger') {
			return true;
		} else {
			return false;
		}
	},//id
	isDD: function () {
		//
		var ua = navigator.userAgent.toLowerCase();
		return ua.indexOf("dingtalk") >= 0;
	},
	//
	isAL: function () {
		var ua = navigator.userAgent.toLowerCase();
		if (ua.match(/Alipay/i) == "alipay") {
			return true;
		} else {
			return false;
		}
	},
	// 
	isQywx: function () {
		var ua = window.navigator.userAgent.toLowerCase();
		if ((ua.match(/MicroMessenger/i) == 'micromessenger') && (ua.match(/wxwork/i) == 'wxwork')) {
			return true;
		} else {
			return false;
		}
	},
	//
	isArray(o) {
		return Object.prototype.toString.call(o) == '[object Array]';
	},

	//null
	isNull: function (exp) {
		if (!exp && typeof exp != 'undefined' && exp != 0) {
			return true;
		} else if (exp == 'null') {
			return true;
		}
		return false;
	},

    /*
     * 
     * params
     * -destination  
     * -source  
     * -miss  
     * */
	deepCopy: function (data) {
		return JSON.parse(JSON.stringify(data));
	},

	trim(str) {
		return str.replace(/(^\s*)|(\s*$)/g, "");
	},

	getAppUserinfo() {
		return new Promise(function (resolve, reject) {
			uni.getSystemInfo({
				success: function (res) {
					resolve(res);
				}
			});
		});
	},
	getRecentVersion() {
		const updateManager = uni.getUpdateManager();

		updateManager.onCheckForUpdate(function (res) {
			// 
			console.log("", res.hasUpdate);
		});

		updateManager.onUpdateReady(function (res) {
			uni.showModal({
				title: '',
				content: '',
				success(res) {
					if (res.confirm) {
						//  applyUpdate 
						updateManager.applyUpdate();
					}
				}
			});

		});

		updateManager.onUpdateFailed(function (res) {
			console.log("");
			// 
		});
	},
	filterTags(data, length) {
		//tag
		if (!data) return [];
		length = length ? length : 12;
		for (var k in data) {
			var tag = data[k].tag;
			if (!tag) {
				data[k].tagArr = [];
				continue;
			}
			tag = tag.replace('', ',').replace('', ',');
			var tagArr = tag.split(',');
			if (tag.length <= length) {
				data[k].tagArr = tagArr;
			} else {
				var indexTab = tag;
				for (var i = 0; i < tagArr.length; i++) {
					var indexNum = indexTab.lastIndexOf(',');
					if (indexNum < length) {
						indexTab = indexTab.substring(0, indexNum)
						data[k].tagArr = indexTab.split(',');
						break;
					} else {
						indexTab = indexTab.substring(0, indexNum);
					}
				}
				data[k].tagArr = indexTab.split(',');
			}
		}
		return data;
	},
	loadScript: function (url, callback) {
		// url , 
		var script = document.createElement('script');
		script.type = 'text/javascript';
		// script
		if (script.readyState) {
			// 
			script.onreadystatechange = function () {
				// readyState
				if (script.readyState === 'complete' || script.readyState === 'loaded') {
					callback();  // 
				}
			}
		} else {
			script.onload = function () {
				callback();  // 
			}
		}
		var href = url.indexOf('?') > -1 ? url += "&v=" + randomRangeId(6) : url += "?v=" + randomRangeId(6)
		script.src = href;
		document.head.appendChild(script);  // head
	},
	getUserInfo() {
		return http.request({
			type: "get",
			url: "/user/info",
		})
	},
	dealTime(str) {
		if (!str) return
		return str.split(' ')[0] + ' ' + str.split(' ')[1].split(':')[0] + ':' + str.split(' ')[1].split(':')[1]
	},
	refreshPageInios() {
		if(tools.ios){
			window.onload = function () {

				var isPageHide = false;
	
				// 
	
				window.addEventListener('pageshow', function () {
	
					if (isPageHide) {
	
						window.location.reload();
	
					}
	
				});
	
				window.addEventListener('pagehide', function () { // 
	
					isPageHide = true;
	
				});
	
			};
		}
	}
};

export default tools