

const filter = {
    // 
    subString(value, length) {
        if (!value) {
            return "";
        }
        value = value.trim();
        if (value.length > length) {
            var first = value.toString().substring(0, length);
            value = first + "...";
        }
        return value;
    },
    // 
    numberFormat(value) {
        value = parseInt(value);
        if (isNaN(value)) {
            return 0;
        }

        if (value >= 10000) {
            value = value + "";
            var len = value.length;
            value = value.slice(0, len - 3);
            return parseFloat(value / 10) + "";
        } else {
            return value
        }
    },
    trustTime(inHisTime, pattern) {
        if (!inHisTime) {
            return;
        }
        inHisTime =
            inHisTime = inHisTime.toString().trim().replace(/-/gm, "/");
        var date = null,
            now = new Date();
        if (inHisTime.indexOf("/") != -1) { //yyyy-mm-dd HH:mm:ss
            date = new Date(inHisTime);
        } else {
            date = new Date(Number(inHisTime)); // long 
        }
        var year = date.getFullYear(),
            month = date.getMonth() + 1,
            day = date.getDate(),
            hours = date.getHours(),
            minute = date.getMinutes();
        var minuteTime = (hours < 10 ? "0" + hours : hours) + ":" + (minute < 10 ? "0" + minute : minute),
            monthTime = (month < 10 ? "0" + month : month) + "-" + (day < 10 ? "0" + day : day);
        if (pattern && pattern.toLowerCase() === "time") {
            return minuteTime;
        } else if (pattern && pattern.toLowerCase() === "date") {
            //  -
            return monthTime
        } else if (pattern && pattern.toLowerCase() === "name") {
            //  -
            return year + "" + month + "" + day+"";
        } else {
            //
            return year + "-" + monthTime + " " + minuteTime;
        }
    },
    dateName(time){
        if(!time)return "";
        var timeArr=time.split("-");
        return timeArr[0]+""+timeArr[1]+""+timeArr[2]+""
    },
    timeStamp(time){
        if(!time)return "";
        var time = new Date(time);
        var y = time.getFullYear();
        var m = time.getMonth()+1;
        var d = time.getDate();
        return y+""+m+""+d+""
    },
	// 
	  mask_pic(content) {
	    if (!content) return '';
	    if (content == 2) {
	      return "";
	    } else if (content == 3) {
	      return "";
	    } else if (content == 4) {
	      return '';
	    }else{
	      return '';
	    }
      },
      auditStatus(type) { //
        var val = "";
        if (type == '0') {
          val = "";
        } else if (type == '1') {
          val = "";
        }
        return val;
      },
      insurederSex(type) { //
        var val = "";
        if (type == '2') {
          val = "";
        }else{
          val = "";
        }
        return val;
      },
}
module.exports = filter